<template>
   <div class="BxPlayer">
      <div class="BxPlayer__BxLeft" v-if="getPlayerData !== 'NTW' ">
         <iframe
            class="BxFrame"
            :src="getPlayerData.player_url" 
            ref="frame" @load="load"
            frameborder="0" 
            allowfullscreen=true
            v-show="iframe.loaded"
         />
   
         <div
            class="BxFrame"
            style="border:none !important;display:flex;justify-content:center;align-items:center;"
            :class="{ BxPreload: getLoader }"
            v-show="!iframe.loaded"
            >
            <v-progress-circular
               :size="80"
               :width="10"
               color="#4dcba1"
               indeterminate
            ></v-progress-circular>
         </div>
      </div>

      <div class="BxPlayer__BxCenter" v-if="getPlayerData === 'NTW' && !getLoader">
         Something Wrong, <br> Please try again Later
      </div>

      <div 
         class="BxPlayer__BxRight"  
         :class="{ BxPreload: getLoader }" 
         v-if="getPlayerData !== 'NTW'">
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Title</span>
            <span>{{getPlayerData.Title | NotEmpty }}</span>
         </div>
         <div class="BxFusion">
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Year</span>
               <span>{{getPlayerData.Year | NotEmpty }}</span>
            </div>
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Released</span>
               <span>{{getPlayerData.Released | NotEmpty }}</span>
            </div>
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Runtime</span>
               <span>{{getPlayerData.Runtime | NotEmpty }}</span>
            </div>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Rated</span>
            <span>{{getPlayerData.Rated | NotEmpty }}</span>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Genre</span>
            <span>{{getPlayerData.Genre | NotEmpty }}</span>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Director</span>
            <span>{{getPlayerData.Director | NotEmpty }}</span>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Writer</span>
            <span>{{getPlayerData.Writer | NotEmpty }}</span>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Actors</span>
            <span>{{getPlayerData.Actors | NotEmpty }}</span>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Plot</span>
            <span>{{getPlayerData.Plot | NotEmpty }}</span>
         </div>
         <div class="BxFusion">
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Language</span>
               <span>{{getPlayerData.Language | NotEmpty }}</span>
            </div>
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Country</span>
               <span>{{getPlayerData.Country | NotEmpty }}</span>
            </div>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
               <span>Awards</span>
               <span>{{getPlayerData.Awards | NotEmpty }}</span>
            </div>
         <div class="BxFusion">
            <div class="BxPlayer__BxRight--BxDetail">
               <span>imdbRating</span>
               <span>{{getPlayerData.imdbRating | NotEmpty }}</span>
            </div>
            <div class="BxPlayer__BxRight--BxDetail">
               <span>imdbVotes</span>
               <span>{{getPlayerData.imdbVotes | NotEmpty }}</span>
            </div>
            <div class="BxPlayer__BxRight--BxDetail">
               <span>Type</span>
               <span>{{getPlayerData.Type | NotEmpty }}</span>
            </div>
         </div>
         <div class="BxPlayer__BxRight--BxDetail">
            <span>Production</span>
            <span>{{getPlayerData.Production | NotEmpty }}</span>
         </div>
         
         
      </div>
      
   </div>
</template>


<script>
   import { AllFilter } from '../Mixins/MixFilter'
   export default {
      mixins: [AllFilter],
      props:[
         'getLoader',
      ],
      data: () => ({ 
         iframe:{
            src:"https://database.gdriveplayer.us/player.php?imdb=tt6193408",
            loaded:false 
         }
      }),

      methods: {
         //This Method to check movie ready to load 
         load(){
            this.iframe.loaded = true
         }
      },

      computed: {
         getPlayerData(){
           return this.$store.state.PlayerData ? this.$store.state.PlayerData.data : "NTW"
         }
      },
      
   }
</script>


<style lang="scss">
   @import './Player.scss';
</style>


